import React, { useState } from "react";
import { Row, Col } from "antd";
import { Link } from "react-router-dom";
import { Input } from "antd";
import Style from "./style";
import { ReactComponent as SearchIcon } from "../../assets/image/search-icon.svg";
import ImageComingSoon from "../../assets/image/coming-soon.png";
import ImageRightArrow from "../../assets/image/right-arrow.png";
import { capabilitiesData } from "./constants";
import { debounce } from "../util";
import Card from "../Card";

const { Search } = Input;

const RegionalizedCapabilities = (props) => {
  const [cards, setCards] = useState(capabilitiesData || []);

  const onSearch = debounce((value) => {
    if (!value?.trim()) {
      setCards(capabilitiesData);
      return;
    }
    value = value?.trim()?.toLowerCase();
    const updatedData = capabilitiesData?.filter((data) =>
      data.text?.toLowerCase()?.includes(value)
    );
    setCards(updatedData);
  });

  return (
    <Style>
      <section>
        <h3 className="head-label">Regionalized Capabilities</h3>
        <Search
          size="large"
          placeholder="Search for relevant folders here..."
          enterButton="Search"
          prefix={<SearchIcon />}
          style={{
            width: "calc(1vw + 40em)",
          }}
          onSearch={(searchString) => onSearch(searchString)}
          onChange={({ target }) => onSearch(target?.value)}
        />

        <Row className="card-wrapper" gutter={[24, 32]}>
          {cards?.map((data, index) => {
            const { flags } = data || {};
            return (
              <Col key={index}>
                <Card
                  dataSource={data}
                  imgSrc={data?.image}
                  title={data?.text}
                  style={{ padding: "0px 0px 10px 10px" }}
                >
                  <div className="flags-row">
                    {flags?.map((flag, subIndex) => {
                      const { image, text, link } = flag || {};
                      return (
                        <div className="flag-data" key={subIndex}>
                          {link ? (
                            <a href={link} target="_blank">
                              <img src={image} />
                            </a>
                          ) : (
                            <img src={image} />
                          )}
                          <div>{text}</div>
                        </div>
                      );
                    })}
                  </div>
                </Card>
              </Col>
            );
          })}
        </Row>

        <Row className="card-wrapper" gutter={[24, 32]}>
          <Col>
            <Card
              imgSrc={ImageComingSoon}
              title="Other Regionalized Capabilities"
              style={{ width: 440 }}
            />
          </Col>

          <Col>
            <Link to="/market-intelligence" className="placeholder dotted">
              <img src={ImageRightArrow} />
              <span>Click here to view</span>
              <strong>Market Intelligence</strong>
            </Link>
          </Col>
        </Row>

        <Row className="note">
          <Col span={24} style={{ textAlign: "center" }}>
            <span>
              We are learning every day. If you are looking for any specific
              content and can't find it here,
            </span>
            <br />
            <span>we will freshly bake it for you. Please contact us.</span>
          </Col>
        </Row>
      </section>
    </Style>
  );
};

export default RegionalizedCapabilities;
